/*==============================================================
.p-faq
==============================================================*/
@use "../../global" as *;

.p-faq {
    &__container {
        padding-top: rem(96);
    }

    &__list {
        margin-top: rem(59);

        @include responsive(lg2) {
            margin-top: rem(112);
        }
    }
    &__item {
        border-bottom: 1px solid $color-accent;
        padding: rem(24) 0;
        cursor: pointer;
        @include responsive(md) {
            padding: rem(36) 0;
        }

        .p-faq__title {
            &.close {
                .toggle {
                    &:after {
                        opacity: 0;
                        transform: translate(-50%, -50%) rotateZ(90deg);
                    }
                }
            }
            &.open {
                .toggle {
                    &::after {
                        opacity: 1;
                        transform: translate(-50%, -50%) rotateZ(0deg);
                    }
                }
            }
        }

        &:first-child {
            border-top: 1px solid $color-accent;

            .p-faq__text {
                display: block;
            }
        }
    }

    &__title {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(14 / 14);
        padding: 0 rem(52) 0 rem(35);
        position: relative;

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(16 / 16);
            padding: 0 rem(52) 0 rem(36);
        }

        .toggle {
            width: rem(20);
            height: rem(20);
            position: absolute;
            top: 0;
            right: 0;
        }

        .toggle:before {
            content: "";
            display: block;
            background: $color-accent;
            width: rem(16);
            height: rem(2);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        .toggle:after {
            content: "";
            display: block;
            background: $color-accent;
            width: rem(2);
            height: rem(16);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            transition:
                transform 0.5s ease,
                opacity 0.5s ease;
        }
    }

    &__title:before {
        content: "Q";
        display: block;
        color: $color-accent;
        font-family: $second-font-family;
        font-size: rem(22);
        font-weight: 600;
        letter-spacing: 0em;
        line-height: calc(22 / 22);
        position: absolute;
        top: 0;
        left: 0;

        @include responsive(md) {
            font-size: rem(24);
            font-weight: 400;
            letter-spacing: 0em;
            line-height: calc(24 / 24);
        }
    }

    &__text {
        display: none;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(14);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(32 / 14);
        padding: 0 rem(52) 0 rem(35);
        margin-top: rem(24);
        position: relative;

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(32 / 16);
            padding: 0 rem(52) 0 rem(65);
            margin-top: rem(35);
        }
    }

    // &__text:before {
    //     content: "A";
    //     display: block;
    //     color: $color-text;
    //     font-family: $second-font-family;
    //     font-size: rem(22);
    //     font-weight: 600;
    //     letter-spacing: 0em;
    //     line-height: calc(22 / 22);
    //     position: absolute;
