    //     left: 0;

    //     @include responsive(md) {
    //         font-size: rem(28);
    //         line-height: calc(28 / 28);
    //     }
    // }
}

.l-section__subTitle {
    &.-faq:not(:first-of-type) {
        margin-top: rem(50);

        @include responsive(md) {
            margin-top: rem(88);
        }
    }
}
