/* p-career
----------------------------------------- */
@use "../../global" as *;

/*==============================================================
.p-career
==============================================================*/
.p-career {
    &__top {
        margin-top: rem(20);

        @include responsive(md) {
            margin-top: rem(114);
        }
    }

    &__text {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(18 / 12);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(16 / 16);
        }
    }

    &__img {
        margin: rem(24) 0 rem(17) 0;

        @include responsive(md) {
            margin: rem(48) 0 rem(37) 0;
        }
    }

    &__list {
        margin-top: rem(40);

        @include responsive(md) {
            margin-top: rem(88);
        }
    }

    &__item:not(:first-child) {
        margin-top: rem(20);

        @include responsive(md) {
            margin-top: rem(38);
        }
    }

    &__head {
        background: $color-sub;
        border-radius: 10px;
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(16);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(16 / 16);
        padding: rem(6) rem(10);

        @include responsive(md) {
            font-size: rem(24);
            line-height: calc(24 / 24);
            padding: rem(12) rem(20);
        }
    }

    &__txt {
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(12);
        font-weight: 400;
        letter-spacing: 0em;
        line-height: calc(20 / 12);
        margin-top: rem(20);

        @include responsive(md) {
            font-size: rem(16);
            line-height: calc(28.6 / 16);
            margin-top: rem(50);
        }
    }
}
