/* c-nav
----------------------------------------- */
@use "../global" as *;

.c-nav {
    @include responsive(lg2) {
        display: flex !important;
        justify-content: space-between;
    }

    &__link {
        display: flex;
        justify-content: center;
        align-items: center;
        background: $color-background;
        border: 1px solid $color-text;
        border-radius: rem(40);
        color: $color-text;
        font-family: $base-font-family;
        font-size: rem(10);
        font-weight: 700;
        letter-spacing: 0em;
        line-height: calc(12 / 12);
        width: 33.333%;
        max-width: 294px;
        text-align: center;
        padding: rem(8) rem(14);

        @include responsive(lg2) {
            font-size: rem(20);
            line-height: calc(20 / 20);
            width: rem(294);
            padding: rem(15) 0;
        }
    }

    &__link:not(:last-child) {
        margin-right: rem(10);
    }

    &__arrow {
        font-size: rem(24);
    }
}
