/* p-details
----------------------------------------- */
@use "../../global" as *;

/*==============================================================
.p-details
==============================================================*/
.p-details {
    &__text {
        margin-top: rem(20);
        letter-spacing: 0em;

        @include responsive(md) {
            margin-top: rem(111);
        }
    }

    &__nav {
        margin-top: rem(68);
    }

    &__tableContainer {
        margin-top: rem(99);
    }
}

.l-section__subTitle {
    &.-details:not(:first-of-type) {
        margin-top: rem(50);

        @include responsive(md) {
            margin-top: rem(88);
        }
    }
}
